    background: -o-linear-gradient(left,#acb6e5, #86fde8);
    background: -webkit-gradient(linear, 0% 0%, 100% 0%, from(#acb6e5), to(#86fde8));
}
#loading {
    position: absolute;
    top: 50%;
    left: 50%;
}
</style>
